:root {
  --navbar-height: 4.5rem;
  --primary-color: #8F95D3;
  --secondary-color: #D3C4E3;
  --info-color: #89DAFF;
  --warn-color: #58504A;
  --gray-color: #e0e0e0;
  --gray-dark: #a8a8a8;
  --gray-darkest: #6f6f6f;
  --light-blue: #697488;
  --secondary-button: #f2f0fd;
}

.container * {
  border-radius: 2px;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}


.container{
  display: flex;
  flex-direction: row-reverse;

}

.container > *{
  margin: 1.5rem;
} 

.basket {
  width: 20vw !important;
}

.products {

  width: 55vw !important;
}

.filters {
  width: 20vw !important; 
}

.footer {
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  text-align: center;
  padding: 4rem 0;
}

button {
  border: 0;
}

/* navbar styling */
.navbar {
  background-color: var(--primary-color);
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  justify-content: flex-end;
  align-items: center;
  height: var(--navbar-height);
  z-index: 99;
  /* padding: 0 var(--x-padding); */
}

html:not([data-scroll='0']) .navbar, .go-up-button {
  background-color: #8f95d3b2;
  backdrop-filter: blur(10px);
}

.go-up-button{
  display: none;
  bottom: 20px;
  right: 30px;
  border-radius: 50%;
  font-size: 2.5rem;
  color: #ffffff;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.281);
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.281);
  height: 4rem;
  width: 4rem;
  z-index: 99;
}

html:not([data-scroll='0']) .go-up-button{
  display: block;
  position: fixed;
}

.navbar > .button {
  display: flex;
  align-items: center;
  height: 100%;
  color: white;
  padding: 0 2.1rem;
  background-color: var(--secondary-color);
  z-index: 1;
  cursor: pointer;
}

.navbar-logo {
  display: table-cell;
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  text-align: center;
}

.navbar-logo strong {
  color: #ffffff;
  cursor: pointer;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 2.5rem;
}
.navbar-logo strong:hover{
color: var(--secondary-color) !important;
}

.price-text{
  color: var(--primary-color);
  font-size: 1.1rem;
  font-weight: 600;
}
/* navbar styling */

/* product card styling */


.product-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0.8rem;
  max-width: 8rem;
  text-align: center;
}


.product-thumbnail {
  display: flex;
  align-content: center;
  justify-content: center;
  padding: 1.2rem 0;
  background-color: #fefefe;
  border: 1.18px solid #f3f0fe;
  border-radius: 12px;
}


.product-thumbnail:hover {
  scale: 1.02;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.13);
}
.product-price {
  color: var(--primary-color);
  font-weight: 600;
  padding: 0.3rem 0;
}
.product-title {
  padding: 0.2rem 0;
  font-weight: 600;
}
.product-button {
  background-color: var(--primary-color);
  color: white;
  cursor: pointer;
  padding: 0.2rem 0;
  width: 100%;
}
.product-button:hover {
  background-color: var(--secondary-color);
  color: white;
}

.products-container {
  /* padding: 0.4rem; */
  display: flex;
  flex-wrap: wrap;
  background-color: white;
  align-content: flex-start;
  justify-content: center;
}


/* product card styling */

.toggle-button {
  margin: 1.1rem 0.6rem 1.3rem 0;
  padding: 0.4rem 1.1rem;
}

/* checkbox radio styling */
.custom-radio[type="radio"] {
  appearance: none;
  border: 1px solid #d3d3d3;
  width: 1.6rem;
  height: 1.6rem;
  content: none;
  outline: none;
  border-radius: 100%;
  margin: 0;
}

.custom-radio[type="checkbox"] {
  appearance: none;
  border: 2px solid var(--gray-color);
  width: 1.6rem;
  height: 1.6rem;
  content: none;
  outline: none;
  border-radius: 100%;
  margin: 0;
}

.custom-radio[type="radio"]:checked {
  appearance: none;
  border-radius: 100%;
  outline: none;
  padding: 0;
  content: none;
  border: 0;
}

.custom-radio[type="radio"]:checked::before {
  position: absolute;
  background: transparent;
  accent-color: var(--primary-color);
  border-radius: 100%;
  color: var(--primary-color) !important;
  content: "\00A0\2713\00A0" !important;
  border: 2px solid var(--primary-color);
  transform: rotate(20deg);
  font-weight: 600;
  font-size: 13px;
}
/* checkbox radio styling */

/* filter container styles */
.filter-container {
  display: flex;
  flex-direction: column;
  background-color: white;
  margin: 0.8rem 0 1.6rem 0;
  padding: 1.8rem 1.6rem 0.9rem 1.6rem;
  border-radius: 2px;
}

.filters-title {
  font-size: 13px;
  font-weight: 600;
  color: var(--light-blue);
}

.filter-item {
  display: flex;
  padding: 0 0 1rem 0;
}
.filter-body {
  width: 100%;
  height: 9rem;
  padding: 1.1rem 0;
  overflow: scroll;
  overflow-x: hidden;
}

input[type="checkbox"],
input[type="radio"] {
  cursor: pointer;
  border-radius: 2px;
}

.sorting-label {
  margin: 0 0 0 0.9rem;
  cursor: pointer;
}
.filtering-label {
  margin: 0 0 0 0.4rem;
  cursor: pointer;
}

.search-bar {
  padding-left: 1.1rem;
  border-radius: 2px !important;
  border-width: 2px;
  border-style: solid;
  border-color: var(--gray-color);
  outline: none !important;
  line-height: 3rem;
}

input[type="text"] {
  outline: none !important;
}


