/* filter container styles */

/* scrollbar style */
.custom-scrollbar::-webkit-scrollbar-track {
  margin-top: 1rem;
  background-color: white;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 5px;
  background-color: #ffffff;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: var(--gray-color);
}
/* scrollbar style */

/* basket styling */
.basket-container {
  padding: 1.35rem;
  background-color: white;
  max-height: 19vh;
  overflow-y: auto;
  border: 2px solid var(--primary-color);
}

.basket-item {
  padding: 1rem 0;
  border-bottom: 1px solid var(--gray-color);
}

.parent {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  align-items: center;
}
.counter-parent {
  grid-area: 1 / 4 / 3 / 7;
}
.price-parent {
  grid-area: 2 / 1 / 3 / 4;
}
.basket-product-name {
  grid-area: 1 / 1 / 2 / 4;
}

.counter-container {
  display: flex;
  justify-content: flex-end;
}

.quantity-button {
  vertical-align: middle;
  background-color: transparent;
  color: var(--primary-color);
  width: 2.3rem;
  height: 2.3rem;
  border-radius: 0 !important ;
}
.counter {
  width: 2.3rem;
  height: 2.3rem;
  border-radius: 0 !important ;
  background-color: var(--primary-color);
  font-weight: 600;
  color: white;
}

.basket-button-container {
  display: flex;
  justify-content: flex-end;
}

.basket-button {
  margin-top: 1.35rem;
  padding: 0.9rem 1.5rem;
  /* margin: 1rem; */
  border: 2px solid var(--primary-color);
  background-color: transparent;
  color: var(--primary-color);
  font-weight: 600;
  cursor: pointer;
}

.empty-cart {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  max-width: 70%;
  margin: auto;

  /* flex-direction: "column"; */
}

/* basket styling */

@media screen and (max-width: 1100px) {
  .container {
    padding: 1rem;
    flex-direction: column;
  }

  .navbar {
    padding: 1rem;
  }

  .navbar > .button {
    padding: 1rem 0.5rem;
  }
  .basket,.products,.filters {
    width: 100% !important; 
  }

  
}
